<template>
  <div class="about">
    <h1>This is an about page</h1>
    <cube-button @click="showPicker">Picker</cube-button>
  </div>
</template>
<script>
const column1 = [
  { text: '剧毒', value: '剧毒'},
  { text: '蚂蚁', value: '蚂蚁' },
  { text: '幽鬼', value: '幽鬼' },
  { text: '幽鬼', value: '幽鬼' },
  { text: '幽鬼', value: '幽鬼' },
  { text: '幽鬼', value: '幽鬼' },
  ]

export default {
  name: 'app',
  data(){
    return{

    }
  },
  mounted(){
   window.onresize = ()=>{
// 　　　console.log('11')
 document.documentElement.clientHeight >= 600 ? this.full() :this.noFull()

  }},
  destoryed(){
 window.onresize = null;
  },
  methods:{
    full(){
  console.log("全屏")
  if(document.getElementsByClassName('cube-picker-panel').length>0){
    document.getElementsByClassName('cube-picker-panel')[0].style.marginBottom="0"
  }
  
},
    noFull(){
  console.log("缩小")
  if(document.getElementsByClassName('cube-picker-panel').length>0){
  document.getElementsByClassName('cube-picker-panel')[0].style.marginBottom="90px"
}
},
    showPicker() {
      if (!this.picker) {
        this.picker = this.$createPicker({
          title: 'Picker',
          data: [column1],
          onSelect: this.selectHandle,
          onCancel: this.cancelHandle
        })
      }
      this.picker.show()
       if(document.getElementsByClassName('cube-picker-panel').length>0){
  document.getElementsByClassName('cube-picker-panel')[0].style.marginBottom="90px"}
   // document.getElementsByClassName('cube-picker-panel')[0].style.height="17.28rem"
    }
  }
}
</script>

